.trainers {
  & h2 {
    font-family: $ff-alumni;
    font-size: 54px;
    line-height: normal;
    letter-spacing: 1.62px;
    padding-bottom: 15px;
    padding-left: 103px;
    margin: 0;

    @include vp-1199 {
      font-size: 42px;
      letter-spacing: 1.26px;
      text-align: center;
      padding-bottom: 0;
      padding-left: 45px;
    }

    @include vp-767 {
      font-size: 32px;
      padding-left: 15px;
    }
  }

  &__container {
    padding-bottom: 70px;

    @include vp-1199 {
      padding-bottom: 65px;
    }

    @include vp-767 {
      padding-bottom: 20px;
    }
  }

  &__items {
    position: relative;
    height: 365px;
    display: flex;
    align-items: center;
  }
}

.trainers__slide {

  cursor: pointer;

  & h3 {
    font-family: $ff-roboto;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    color: $color-default-white;
    margin-top: -40px;
    margin-left: 44%;
    transform: translate(-30%,-45%);

    @include vp-1199 {
      margin-left: 48%;
    }
  }
}

.trainers__slide:hover .trainers__text {
  visibility: visible;
}

.trainers__slide:hover > h3 {
  display: none;
}

.trainers__image {
  position: relative;
  width: 260px;
  height: 294px;

  @include vp-1199 {
    width: 268px;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 226px;
    height: 274px;
  }
}

.trainers__image img {
  width: 260px;
  height: 294px;
  object-fit: cover;

  @include vp-1199 {
    width: 268px;
  }

  @include vp-767 {
    width: 226px;
    height: 274px;
  }
}

.trainers__text {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 260px;
  height: 294px;
  background-color: rgba(16, 38, 84, 0.89);
  box-shadow: 0 0 40px rgba(28, 51, 116, 0.2);
  color: $color-default-white;
  font-family: $ff-roboto;
  font-weight: 400;
  padding-top: 53px;
  visibility: hidden;
  transition: $trans-default;

  @include vp-1199 {
    width: 268px;
    margin: 0 auto;
    padding-top: 55px;
  }

  @include vp-767 {
    width: 226px;
    height: 274px;
  }

  & h3 {
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
  }

  & p {
    color: #b6bdcc;
    font-size: 16px;
    text-align: center;
    margin: 0;
    padding-bottom: 35px;
  }
}

.trainers__text-list {
  width: 80%;
  margin: 0 auto;
  overflow-y: scroll;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  height: 50%;

  & li {
    font-size: 14px;
    margin: 0;
    padding: 0;
  }
}

.trainers__swiper-button svg {
  display: block;
  width: 16px;
  height: 32px;
}

.trainers__swiper-button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid $color-transparent;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  top: 47.7%;

  @include vp-1199 {
    top: 48.6%;
  }

  @include vp-767 {
    top: 41.5%;
  }

  &--prev {
    left: 27px;

    @include vp-767 {
      left: 0;
    }
  }

  &--next {
    transform: rotate(180deg);
    right: 27px;

    @include vp-767 {
      right: 0;
    }
  }
}

.trainers__swiper-button:hover {
  border: 1px solid $color-default-black;
  border-radius: 50%;
  width: 56px;
  height: 56px;
  background-position: 50%;
}
